Navbar.defaultProps = {
  title : "Enter your own title ",
  SearchBar : true
}


Navbar.propTypes = {
  title : PropTypes.string,
  SearchBar : PropTypes.bool
}
//propTypes to make sure only values are sent in the mentioned data types else it throws error in console